import { __ } from '@wordpress/i18n';
import {
	useBlockProps,
	RichText,
	MediaPlaceholder,
	BlockControls,
	MediaReplaceFlow,
	InspectorControls,
	InnerBlocks
} from '@wordpress/block-editor';
import {
	withNotices,
	Spinner,
	ToolbarButton,
	PanelBody,
	TextareaControl
} from '@wordpress/components';
import { isBlobURL, revokeBlobURL } from '@wordpress/blob';
import { useEffect, useState } from '@wordpress/element';

import './editor.scss';

function Edit({ attributes, setAttributes, noticeOperations, noticeUI }) {
	const { title, info, alt, url, id } = attributes;
	const [blobURL, setBlobURL] = useState();



	// const onChangeTitle = (newTitle) => {
	// 	setAttributes({ title: newTitle });
	// };

	// const onChangeSubtitle = (newSubtitle) => {
	// 	setAttributes({ subtitle: newSubtitle });
	// };

	const onSelectImage = (image) => {
		if (!image || !image.url) {
			setAttributes({ url: undefined, id: undefined, alt: '' });
			return;
		}
		setAttributes({ url: image.url, id: image.id, alt: image.alt });
	};
	const onSelectURL = (newURL) => {
		setAttributes({ url: newURL, id: undefined, alt: '' });
	};

	const onUploadError = (message) => {
		noticeOperations.removeAllNotices();
		noticeOperations.createErrorNotice(message);
	};

	const removeImage = () => {
		setAttributes({
			url: undefined,
			alt: '',
			id: undefined,
		});
	};

	const onChangeAlt = (newAlt) => {
		setAttributes({ alt: newAlt });
	};

	useEffect(() => {
		if (!id && isBlobURL(url)) {
			setAttributes({
				url: undefined,
				alt: '',
			});
		}
	}, []);

	useEffect(() => {
		if (isBlobURL(url)) {
			setBlobURL(url);
		} else {
			revokeBlobURL(blobURL);
			setBlobURL();
		}
	}, [url]);

	return (
		<>
			{ /* Image settings */}

			{/* <InspectorControls>
				<PanelBody title="Image Settings">
					{url && !isBlobURL(url) && (
						<TextareaControl
							label="Alt text"
							value={alt}
							onChange={onChangeAlt}
							help={
								'Alt text is good to explain search engines and for people who can not read it'
							}
						/>
					)}
				</PanelBody>


			</InspectorControls>
			{url && (
				<BlockControls group="inline">
					<MediaReplaceFlow
						onSelect={onSelectImage}
						onSelectURL={onSelectURL}
						onError={onUploadError}
						accept="image/*"
						allowedTypes={['image']}
						mediaId={id}
						mediaURL={url}
					/>
					<ToolbarButton onClick={removeImage}>
						Remove Image
					</ToolbarButton>
				</BlockControls>
			)} */}

			{ /* End of Image settings */}


			<section {...useBlockProps({
				className: 'max-w-7xl mx-auto text-center sm:px-8 py-24 ffre'
			})}>
				<RichText
					className="text-2xl font-bold text-gray-800 sm:text-3xl lg:text-4xl"
					placeholder="Title"
					tagName="h2"
					value={title}
					onChange={(newTitle) => setAttributes({ title: newTitle })}
				/>
				<RichText
					className="mt-2 text-gray-600"
					placeholder="Title"
					tagName="p"
					value={info}
					onChange={(newInfo) => setAttributes({ info: newInfo })}
				/>

				{/* <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-4 py-12"> */}
					<InnerBlocks
						allowedBlocks={[
							'create-block/single-service',
						]}
					/>
				{/* </div> */}
			</section>
		</>
	);
}

export default withNotices(Edit);
